// FOR SLIDER WITH ARROWS BUT WITHOUT DOTS

.slider__arrow-btn {
  @include zero-edges;
  @include centering-flex;

  outline: none;
  border: none;
  background-color: transparent;

  position: absolute;
  top: 50%;
  transform: translateY(-50%);

  width: 2.5em;
  height: 2.5em;
  border-radius: 50%;
  box-shadow: none;

  transition: all 0.3s ease-in;
  cursor: pointer;

  &:before {
    content: "";
    display: block;
    width: 30%;
    height: 30%;
    border: 0.2em solid $lcg-1;
    border-right-color: transparent;
    border-bottom-color: transparent;
  }

  &--previous {
    left: 0;

    &:before {
      transform: translateX(0.11em) rotate(-45deg);
    }
  }

  &--next {
    left: 100%;
    transform: translate(-100%, -50%);

    &:before {
      transform: translateX(-0.15em) rotate(135deg);
    }
  }

  &:focus,
  &:hover,
  &:active {
    box-shadow: inset 0 0 0.5em $lcg-1;
  }
}
